/*
    to use styles without modifier class on .inline-icon
    use @extend

    e.g. style icon on parent hover:
        a:hover .inline-icon {
            @extend %inline-icon.active;
        }
*/

%inline-icon {
    @include themify($themes) {
        @include colorIcon(themed('text_primary_color'), none);

        &.active {
            @include colorIcon(themed('navbar_background_color'), themed('navbar_active_color'), themed('navbar_active_color'), none);
        }
        &.disabled {
            @include colorIcon(themed('disabled_color'), none);
        }
        &.white {
            @include colorIcon(themed('text_color'));
        }
        &.border_hover_color {
            @include colorIcon(themed('border_hover_color'));
        }
        &.secondary {
            @include colorIcon(themed('text_secondary_color'));
        }
    }
}

