    stats              : #f3f3f3,

    utility            : #f3f3f3,
    math               : #f3f3f3,
    text               : #f3f3f3,
    logic              : #f3f3f3,
    lists              : #f3f3f3,
    custom_functions   : #f3f3f3,
    variables          : #f3f3f3,
    loops              : #f3f3f3,
    time               : #f3f3f3,
    misc               : #f3f3f3,
    notifications      : #f3f3f3
);

.toolbox {
    $toolbox         : &;
    $white           : #fff;
    $lightgrey       : #ebebeb;
    $grey            : #f4f4f6;
    $gray20          : #333;
    $category-text-spacing: 10px;

    background-color: $white;
    border: 1px solid $lightgrey;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    max-height: calc(100% - 40px);
    overflow: hidden;
    position: absolute;
    user-select: none;
    z-index: 1;
    width: 250px;

    &__header {
        padding: 0.5em;
        position: relative;
        font-weight: bold;
        font-size: 1.6em;
        background-color: $grey;
        text-align: center;
    }
    &__item {
        display: flex;
        flex-direction: row;
    }
    &__category-arrow {
        display: flex;
        margin: auto;

        &--back {
            margin-left: $category-text-spacing;
            fill: $gray20;
            transform: rotate(90deg);
        }
        &--open {
            margin-right: $category-text-spacing;
            transform: rotate(270deg);
        }
    }
    &__category-colour {
        width: 5px;
        flex-shrink: 0;
    }
    @each $category, $colour in $category-colours {
        &__category {
            &--#{$category} {
                #{$toolbox}__category-colour {
                    background-color: $colour;
                }
            }
        }
        &__category-return {
            #{$toolbox}__category {
                &--#{$category} {
                    background-color: $lightgrey;
                    color: $gray20;
                    max-height: 38px;
                }
            }
        }
    }

    &__category-menu {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        min-height: 0;
        overflow-y: auto;
        overflow-x: hidden;
    }
    &__category-return {
        cursor: pointer;
        text-align: center;

        #{$toolbox}__label {
            margin-left: -($category-text-spacing * 2);
        }
    }
    &__category-text {
        align-self: center;
        margin: $category-text-spacing;
        width: 100%;
    }
    &__description {
        font-size: 12px;
        line-height: 1.4;
    }
    &__icon {
        display: flex;
        align-self: center;
        margin-right: $category-text-spacing;

        > svg {
            height: 25px;
            width: 25px;
        }
    }
    &__label {
        font-weight: bold;
        font-size: 14px;
        line-height: 1.4;
    }
    &__row {
        cursor: pointer;
        border-top: 1px solid $lightgrey;

        &:last-of-type {
            border-bottom: 1px solid $lightgrey;
        }

        @each $category, $colour in $category-colours {
            #{$toolbox}__category {
                &--#{$category} {
                    &:hover:not(#{$toolbox}__category--selected) {
                        background-image: linear-gradient(to right, $white 25%, transparentize($colour, 0.8) 100%);
                    }
                }
                &--#{$category}#{$toolbox}__category--selected {
                    background-color: transparentize($colour, 0.9);
                    transition: background-color 0.25s ease;
                }
            }
        }
    }
    &__separator {
        padding: 10px 0;
        border-top: 1px solid $lightgrey;
    }
    #search_input {
        padding: 10px;
        border-radius: 5px;
        border: solid $grey 1px;
        margin: 3px;
        font-size: 14px;
        font-weight: 700;
    }
}